.overlay {
  /*    */
  opacity: 0;
  visibility: hidden;

  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 200;
  transition: 0.3s all;

  overflow-y: scroll;
}

/*   */

.modal {
  /*    */
  opacity: 0;
  visibility: hidden;

  /*     z-index */
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 300; /*      */
}

/*     */

.modal-wrapper {
  position: relative;
}

/*        */

.modal.active,
.overlay.active {
  opacity: 1;
  visibility: visible;
}

